import React from "react";
import "./modals.scss";

let openModal;
let closeModal;
let copyToClipboard;
let openImageModal;

class Modals extends React.Component
{
	constructor()
	{
		super();

		this.state =
		{
			active: false,
			modal: null
		};
	}

	componentDidMount()
	{
		openModal = modal =>
		{
			this.setState({ modal, active: true });
		};

		closeModal = () =>
		{
			if (this.state.active)
			{
				this.setState({ active: false });
			}
		};

		copyToClipboard = text =>
		{
			navigator.clipboard.writeText(text);

			openModal((
				<div className="ClipboardModal" onClick={() => closeModal()}>
					<span>
						"{text}" copied to clipboard!
					</span>
				</div>
			));
		};

		openImageModal = url =>
			openModal(
				<div className="ImageModal" onClick={() => closeModal()} style={{ backgroundImage: `url(${url})` }}/>
			);
	}

	render()
	{
		return (
			<div className={"ModalContainer" + (this.state.active ? " Active" : "")}
				onClick={e => {if (e.target == e.currentTarget || e.target.classList.contains("Modal")) closeModal()}}>
				<div className="Modal">
					{ this.state.modal }
				</div>
			</div>
		);
	}
}

export { Modals, openModal, closeModal, copyToClipboard, openImageModal };